$color-headings: #333333;
$color-gray: #EEEEEE;

h4 {
  color: $color-headings;
  margin-top: 0;
}

.txt-catalog {
  display: block;
  color: $color-headings;
  font-size: 15px;
  border-radius: 3px;
  padding: 0.5em;
  margin-left: 0.5em;
}

.txt-catalog:hover {
  background-color: $color-gray;
}

.list-catalog {
  list-style-type: none;
  padding: 0.1em 0;
}

.list-catalog-h3 {
  border-left: solid $color-gray 1px;
  margin-left: 1em;
}

.txt-catalog:hover {
  background-color: $color-gray;
}

#box-catalog {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}